import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)

export default new Router({
    mode: 'history',
    routes: [{
            path: '/',
            component: resolve => require(['./pages/Home.vue'], resolve),
            meta: {
                auth: true
            },
            name: 'Home'
        }, //首页
        {
            path: '/Home',
            component: resolve => require(['./pages/Home.vue'], resolve),
            meta: {
                auth: true
            },
            name: 'Home'
        },{
            path: '/DetailShare',
            component: resolve => require(['./pages/ArticleDetail.vue'], resolve),
            meta:{
                auth: true
            },
            name: 'DetailShare'
        }]
})